<template>
    <el-container>
        <el-header>
            <ul class="menu">
                <li v-for="(tab,index) in tabs" v-bind:key="index" class="menuItem"> 
                    <router-link :to="tab.id">{{tab.name}}</router-link>
                </li>
            </ul>
        </el-header>
        <el-main>
            <router-view></router-view>
        </el-main>
    </el-container>
</template>
<script>
export default {
  name: 'Home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      tabs:[
        {
          id:"Note",
          name:"关注"
        },
        {
          id:"Recommend",
          name:"推荐"
        },
        {
          id:"Msg",
          name:"热点"
        },
        {
          id:"Mine",
          name:"娱乐"
        },
        {
          id:"Msg",
          name:"科技"
        },
        {
          id:"Mine",
          name:"财经"
        },
        {
          id:"Mine",
          name:"数码"
        },
      ]
    }
  }
}
</script>
<style scoped>
.el-header{
    height: 2em !important;
    background-color: #FEFEFE; 
    padding: 0% !important;
}
.el-main{
  padding: 0em;
  margin: 0em;
}
.menu{
  width: 100%;
  margin: 0px;
  padding: 0px; 
}
.menuItem{
  margin: 0px;
  padding: 0px;
  width: 4em;
  float: left;
  text-align: center;
  list-style-type: none;
  line-height: 2em; 
}
.menuItem a{
  text-decoration: none;
  color: #858585;
}
</style>


