import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Home from '@/components/Home'
import Map from '@/components/Map'
import Msg from '@/components/Msg'
import Mine from '@/components/Mine'

import Note from '@/components/widgets/Note'
import Recommend from '@/components/widgets/Recommend'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main,
      children: [
        {
          path: '/Home',
          name: 'Home',
          component: Home,
          children: [
            {
              path: '/Note',
              name: 'Note',
              component: Note
            },
            {
              path: '/Recommend',
              name: 'Recommend',
              component: Recommend
            },
          ]
        },
        {
          path: '/Map',
          name: 'Map',
          component: Map
        },
        {
          path: '/Msg',
          name: 'Msg',
          component: Msg
        },
        {
          path: '/Mine',
          name: 'Mine',
          component: Mine
        },
      ]
    },
    
  ]
})



