<template>
    <div style="height:100%">
        <iframe :style="{height:height}"
        src="http://www.qingdaonews.com/node/node_144470.htm" frameborder="0"></iframe>
    </div>
</template>

<script>
export default {
  name: 'Note',
  data () {
    return {
      height:"29em",
    }
  }
}
</script>

<style scoped>
div{
    overflow: hidden;
}
iframe{
    overflow: hidden;
    width: 100%;
}
</style>


