<template>
  <el-container>
    <el-main class="content">
      <router-view></router-view>
    </el-main>
    <el-footer>
     <ul class="menu">
       <li v-for="(tab,index) in tabs" v-bind:key="index" class="menuItem"> 
         <router-link :to="tab.id">{{tab.name}}</router-link>
       </li>
     </ul>
    </el-footer>
  </el-container>
</template>

<script>
export default {
  name: 'Main',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      tabs:[
        {
          id:"Home",
          name:"首页"
        },
        {
          id:"Map",
          name:"地图"
        },
        {
          id:"Msg",
          name:"消息"
        },
        {
          id:"Mine",
          name:"我的"
        },
      ]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-container{
  height: 100%;
}
.el-footer{
  background-color: #FEFEFE;
  height: 3em !important;
}
.el-main{
  padding: 0em;
  margin: 0em;
}
.content{
  height: 100%;
}
.menu{
  width: 100%;
  margin: 0px;
  padding: 0px;
  
}
.menuItem{
  margin: 0px;
  padding: 0px;
  width: 25%;
  float: left;
  text-align: center;
  list-style-type: none;
  line-height: 3em; 
}
.menuItem a{
  text-decoration: none;
  color: #858585;
}
</style>



